.menu__toggle {
    position: absolute;
    z-index: 100;
    top: 0;
    right: 0;
    cursor: pointer;
    font-size: 15px;
    line-height: 20px
}

@media only screen and (min-width: 1024px) {
    .menu__toggle {
        display: none
    }
}

.menu__toggle__icon {
    float: right;
    position: relative;
    width: 24px;
    height: 20px;
    margin-left: 8px
}

.menu__toggle__icon span, .menu__toggle__icon span:after, .menu__toggle__icon span:before {
    content: '';
    position: absolute;
    top: 8px;
    left: 0;
    width: 100%;
    height: 3px;
    -webkit-transition: all .1s;
    -moz-transition: all .1s;
    -ms-transition: all .1s;
    -o-transition: all .1s;
    transition: all .1s;
    background: rgba(255, 255, 255, .7)
}

.menu__toggle__icon span:before {
    top: -8px
}

.menu__toggle__icon span:after {
    top: 8px
}

.menu--open .menu__toggle__icon span:after, .menu--open .menu__toggle__icon span:before {
    background: #fff !important
}

.menu--open .menu__toggle__icon span {
    background-color: transparent !important
}

.menu--open .menu__toggle__icon span:after, .menu--open .menu__toggle__icon span:before {
    top: 0
}

.menu--open .menu__toggle__icon span:before {
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg)
}

.menu--open .menu__toggle__icon span:after {
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg)
}

.menu__wrap {
    position: fixed;
    z-index: 90;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(22, 27, 33, .95);
