.contact-form {
    background: #F5F7FA;
    margin: 20px auto;
    width: 100%;
    padding: 20px
}

@media only screen and (min-width: 768px) {
    .contact-form {
        margin: 30px auto;
        padding: 40px
    }
}

@media only screen and (min-width: 1024px) {
    .contact-form {
        max-width: calc(80% + 80px)
    }
}

@media only screen and (min-width: 1220px) {
    .contact-form {
        margin: 40px auto;
        max-width: calc(75% + 100px);
        padding: 50px
    }
}

@media only screen and (min-width: 1600px) {
    .contact-form {
        max-width: calc(70% + 120px);
        padding: 60px
    }
}

.contact-form:first-child {
    margin-top: 0
}

.contact-form__item {
    margin-top: 15px
}

@media only screen and (min-width: 768px) {
    .contact-form__item {
        margin-top: 20px
    }
}

@media only screen and (min-width: 1220px) {
    .contact-form__item {
        margin-top: 30px
    }
}

.contact-form__item:first-child {
    margin-top: 0
}

.contact-form__item--error .contact-form__input, .contact-form__item--error .contact-form__textarea {
    border-color: #D64541
}

.contact-form__label {
    display: block;
    font-size: 16px;
    font-weight: 600;
    color: #6C7A89
}

@media only screen and (min-width: 768px) {
    .contact-form__label {
        font-size: 18px
    }
}

@media only screen and (min-width: 1220px) {
    .contact-form__label {
        font-size: 20px
    }
}

.contact-form__input, .contact-form__textarea {
    width: 100%;
    margin-top: 8px;
    border: 2px solid #ddd;
    border-radius: 2px;
    padding: 10px 12px;
    background: #fff;
    font-size: 16px;
    line-height: 1.4;
    color: #2A2F36;
    -webkit-transition: all .15s ease-in-out;
    -moz-transition: all .15s ease-in-out;
    -ms-transition: all .15s ease-in-out;
    -o-transition: all .15s ease-in-out;
    transition: all .15s ease-in-out
}

@media only screen and (min-width: 768px) {
    .contact-form__input, .contact-form__textarea {
        padding: 15px 18px;
        font-size: 18px
    }
}

@media only screen and (min-width: 1220px) {
    .contact-form__input, .contact-form__textarea {
        font-size: 20px
    }
}

.contact-form__input:focus, .contact-form__textarea:focus {
    outline: 0;
    border-color: #A2DED0
}

::-webkit-input-placeholder {
    color: #d4dada
}

::-moz-placeholder {
    color: #d4dada
}

:-ms-input-placeholder {
    color: #d4dada
}

:-moz-placeholder {
    color: #d4dada
}

.contact-form__textarea {
    min-height: 200px;
    resize: none
}

.portfolio {
    margin-top: 0;
    padding: 20px 0 0
}

@media only screen and (min-width: 768px) {
    .portfolio {
        padding: 35px 0 0
    }
}

@media only screen and (min-width: 1024px) {
    .portfolio {
        padding: 35px 40px 0
    }
}

@media only screen and (min-width: 1220px) {
    .portfolio {
        padding: 53px 60px 0
    }
}

.portfolio-item {
    position: relative;
    margin-top: 20px
}

@media only screen and (min-width: 768px) {
    .portfolio-item {
        margin-top: 35px
    }
}

@media only screen and (min-width: 1024px) {
    .portfolio-item {
        margin-top: 40px
    }
}

@media only screen and (min-width: 1220px) {
    .portfolio-item {
        margin-top: 60px
    }
}

.portfolio-item:first-child {
    margin-top: 0
}

.portfolio-item__link {
    position: relative;
    display: block
}

@media only screen and (min-width: 1024px) {
    .portfolio-item__link:hover .portfolio-item__content {
        opacity: 1;
        visibility: visible
    }
}

.portfolio-item__image {
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat
}

.portfolio-item__content {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(22, 27, 33, .7);
    opacity: 0;
    visibility: hidden;
    -webkit-transition: all .2s ease-in-out;
    -moz-transition: all .2s ease-in-out;
    -ms-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out
}

.portfolio-item__info {
    text-align: center
}

.portfolio-item__title {
    color: #fff
}

.portfolio-item__subtitle {
    margin-top: 5px;
    color: rgba(255, 255, 255, .7)
}

.blog {
    margin-top: 0;
    padding: 20px 0 0
}

@media only screen and (min-width: 768px) {
    .blog {
        padding: 35px 0 0
    }
}

@media only screen and (min-width: 1024px) {
    .blog {
        padding: 35px 40px 0
    }
}

@media only screen and (min-width: 1220px) {
    .blog {
        padding: 53px 60px 0
    }
}

.blog-post {
    margin-top: 40px
}

@media only screen and (min-width: 768px) {
    .blog-post {
        margin-top: 60px
    }
}

@media only screen and (min-width: 1024px) {
    .blog-post {
        margin-top: 80px
    }
}

@media only screen and (min-width: 1220px) {
    .blog-post {
        margin-top: 100px
    }
}

.blog-post:first-child {
    margin-top: 0
}

.blog-post__image {
    margin-top: 20px;
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat
}

@media only screen and (min-width: 768px) {
    .blog-post__image {
        margin-top: 25px
    }
}

@media only screen and (min-width: 1024px) {
    .blog-post__image {
        margin-top: 30px
    }
}

@media only screen and (min-width: 1220px) {
    .blog-post__image {
        margin-top: 40px
    }
}

.blog-post__content {
    margin-top: 20px
}

@media only screen and (min-width: 768px) {
    .blog-post__content {
        margin-top: 25px
    }
}

@media only screen and (min-width: 1024px) {
    .blog-post__content {
        margin-top: 30px
    }
}

@media only screen and (min-width: 1220px) {
    .blog-post__content {
        margin-top: 40px
    }
}

.blog-post__content p .button {
    margin-top: 5px
}

@media only screen and (min-width: 1024px) {
    .blog-post__content p .button {
        margin-top: 10px
    }
}

.blog-post__subtitle {
    margin-top: 5px;
    color: #ABB7B7
}

.pagination {
    margin-top: 0;
    padding: 40px 20px 0
}

@media only screen and (min-width: 768px) {
    .pagination {
        padding: 55px 40px 0
    }
}

@media only screen and (min-width: 1024px) {
    .pagination {
        padding: 75px 40px 0
    }
}

@media only screen and (min-width: 1220px) {
    .pagination {
        padding: 93px 60px 0
    }
}

.pagination__next, .pagination__prev {
    width: 50%
}

.pagination__next .button, .pagination__prev .button {
    display: block;
    text-align: center
}

.pagination__prev {
    float: left;
    padding-right: 5px
}

.pagination__next {
    float: right;
    padding-left: 5px
}